@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&family=Rubik:wght@400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{margin:0;padding:0;box-sizing:border-box}html{font-size:10px}@media (min-width: 640px){html{font-size:10px}}@media (min-width: 1035px){html{font-size:11px}}body{font-family:"Raleway",sans-serif;font-size:1.4938rem;color:#000;padding:45px 25px;line-height:1.5em}.app{min-height:100vh;width:100%;display:flex;flex-direction:column}@media (min-width: 1035px){.app{max-width:60%;margin:0 auto}}.wrapper{flex-grow:1;width:100%;display:flex;flex-wrap:wrap;align-content:flex-start}main{width:100%}@media (min-width: 640px){main{width:75%;margin-left:25%}}@media (min-width: 1035px){main{width:75%;margin-left:25%}}header{width:100%}@media (min-width: 640px){header{width:25%;position:fixed}}@media (min-width: 1035px){header{width:15%}}a{text-decoration:none;color:inherit}h1,h2,h3,h4,h5,h6{line-height:1.5em;font-family:"Rubik",sans-serif;font-weight:400}h1{text-transform:uppercase;font-weight:500}button{border:2px solid #000;background:none;color:#000}button:hover{color:#000}span.grey{color:#9a9a9a !important}img{max-width:100%;max-height:100%}li{font-size:16.83px;list-style-type:none}.header__title{font-family:"Rubik",sans-serif;margin-bottom:60px}.footer{margin-top:40px;margin-bottom:45px}@media (min-width: 640px){.footer{margin-left:25%}}.footer__text{text-align:center}.nav{margin-bottom:20px;padding-right:30px}.nav__mobile{position:fixed;top:0;left:0;width:100%;height:100%;z-index:999;background:#000;padding:45px 25px}.nav__mobile .header__title{color:#fff}.nav__list--collapsed{display:none}@media (min-width: 640px){.nav__list--collapsed{display:block}}.nav__list--is-expanded{display:block;margin-top:100px;color:#fff}.nav__list-item{display:flex;padding:0;padding-left:5px;text-align:left;font-size:20px}.nav__list-item a{padding:15px 0px 5px 0px;font-size:16.85px;font-family:"Rubik",sans-serif;text-transform:uppercase}.nav__list-item a:hover{border-bottom:2px solid #000}.nav__flag-wrapper{display:flex;align-items:center}.nav__flag{width:20px;height:auto;margin-right:10px;display:block}.nav__toggle{display:block;margin-right:0;margin-left:auto;position:absolute;top:50px;right:25px;border:none}.nav__toggle:hover{border:none}@media (min-width: 640px){.nav__toggle{display:none}}.nav__toggle-image{width:36px;height:auto}.project{display:flex;flex-wrap:wrap}.project__gallery{flex-basis:100%}@media (min-width: 640px){.project__gallery{flex-basis:60%}}@media (min-width: 1035px){.project__gallery{flex-basis:75%}}.project__description{padding-left:0;flex-basis:100%;order:-1;margin-bottom:20px}@media (min-width: 640px){.project__description{flex-basis:40%;order:0;padding-left:20px}}@media (min-width: 1035px){.project__description{flex-basis:25%;order:0;padding-left:20px}}.project__item{padding:5px 0;background:#ebebec;margin-bottom:10px}@media (min-width: 640px){.project__item{padding:7px 0;margin-bottom:15px}}.project__desc-header{font-weight:600;margin-top:15px}.project__title{margin-bottom:40px}.about__text{margin-top:10px}.about__title{margin-top:0px;margin-bottom:20px;margin-top:0px}.about__title--separate{margin-top:40px}.contact__text{margin-top:10px}.contact__title{margin-top:0px;margin-bottom:20px;margin-top:0px}.portfolio{display:flex;flex-wrap:wrap;width:100%}.portfolio__item{flex-basis:100%;height:auto;padding:7px 0px}@media (min-width: 640px){.portfolio__item{flex-basis:50%;padding:7px}}@media (min-width: 1600px){.portfolio__item{flex-basis:33%}}.portfolio__item:hover .portfolio__overlay{opacity:1}.portfolio__inner{background:#ebebec;position:relative}.portfolio__overlay{position:absolute;align-items:center;justify-content:center;flex-wrap:wrap;align-content:center;top:0%;left:0%;width:100%;height:100%;background:rgba(0,0,0,0.85);display:flex;transition:.3s;opacity:0}.portfolio__category{color:#fff;flex-basis:100%;text-align:center;margin-bottom:20px;letter-spacing:0.05em}.portfolio__title{color:#fff;flex-basis:100%;text-align:center;margin-bottom:40px;padding:0 12px}@media (min-width: 1035px){.portfolio__title{font-size:20.659px}}@media (min-width: 1600px){.portfolio__title{font-size:20.659px;margin-bottom:20px}}.portfolio__btn{margin:40px auto;display:block;padding:20px 40px;text-transform:uppercase;font-family:"Rubik",sans-serif;font-weight:400;transition:.3s}.portfolio__btn:hover{background:#000;color:#fff}.portfolio__btn-view{margin:auto;display:block;padding:15px 25px;text-transform:uppercase;font-family:"Rubik",sans-serif;font-weight:400;font-size:16.85px;border:2px solid white;background:none;color:#fff;transition:.3s}.portfolio__btn-view:hover{background:rgba(255,255,255,0.7);color:black}.portfolio__filter{background:none;color:#000;text-align:left;width:100%;margin-bottom:40px;text-transform:uppercase;font-family:"Rubik",sans-serif;font-weight:400;display:block}@media (min-width: 640px){.portfolio__filter{width:50%;margin-left:auto;margin-right:5px;text-align:left;padding-left:10px}}.portfolio__filter>div>div{border:2px solid #000;border-radius:0}

.fade-appear {
  opacity: 0;
  z-index: 1;
}
.fade-appear.fade-appear-active {
  opacity: 1;
  transition: 700ms linear;
}

.fade-enter {
  opacity: 0;
  z-index: 1;
}
.fade-enter.fade-enter-active {
  opacity: 1;
  transition: 700ms linear;
}

